.banner{
    height: 500px;
    object-fit: cover;
    background-size: cover;
    background-position: center center;
    color: #ffffff;
}

.contents{
    padding-top: 190px;
    padding-left: 30px;
    max-height: 140px;
}

.banner_details{
    padding-top: 12px;
    font-size: 0.8rem;
    max-width: 60vw;
}

@media only screen and (min-width: 768px){
    .banner_details{
        max-width: 40vw;
    }
}

.title{
    font-size: 2rem;
    padding-bottom: 10px;
}

@media only screen and (max-width: 768px){
    .title{
        font-size: 1.8rem;
        max-width: 70vw;
    }
}

.banner_buttons > button {
    border: none;
    color: #ffffff;
    margin-right: 20px;
    border-radius: 2px;
    padding: 8px 25px;
    font-size: 1rem;
    background-color: rgba(51, 51, 51, 0.4);
}

.play_btn:hover{
    transition: transform 600ms;
    background-color: rgb(202, 39, 39);
}

.list_btn:hover{
    transition: transform 400ms;
    color: #000;
    background-color: rgb(206, 200, 200);
}

.fade_bottom{
    height: 11rem;
    background-image: linear-gradient(180deg, transparent, rgba(31, 29, 29, 0.61),#000);
}

@media only screen and (min-width: 768px){
    .contents{
        padding-top: 220px;
    }

    .fade_bottom{
        height: 9rem;
    }
}