@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);


*{
    margin: 0;
}

body{
    color: #fff;
    background-color: #000;
    font-family: 'Noto Sans JP', sans-serif;
}
.row{
    margin: 20px;
}

.row__movies{
    display: -webkit-flex;
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row__movies::-webkit-scrollbar{
    display: none;
}

.item__movie{
    object-fit: contain;
    margin-right: 10px;
    width: 100%;
    height: auto;
    max-height: 200px;
}

.item__movielarge{
    object-fit: contain;
    margin: 10px;
    width: 100%;
    height: auto;
    max-height: 300px;
}

.item__movielarge:hover{
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
    transition: -webkit-transform 400ms;
    transition: transform 400ms;
    transition: transform 400ms, -webkit-transform 400ms;
}



.item__movie:hover{
    -webkit-transform: scale(1.10);
            transform: scale(1.10);
    transition: -webkit-transform 400ms;
    transition: transform 400ms;
    transition: transform 400ms, -webkit-transform 400ms;
}
.banner{
    height: 500px;
    object-fit: cover;
    background-size: cover;
    background-position: center center;
    color: #ffffff;
}

.contents{
    padding-top: 190px;
    padding-left: 30px;
    max-height: 140px;
}

.banner_details{
    padding-top: 12px;
    font-size: 0.8rem;
    max-width: 60vw;
}

@media only screen and (min-width: 768px){
    .banner_details{
        max-width: 40vw;
    }
}

.title{
    font-size: 2rem;
    padding-bottom: 10px;
}

@media only screen and (max-width: 768px){
    .title{
        font-size: 1.8rem;
        max-width: 70vw;
    }
}

.banner_buttons > button {
    border: none;
    color: #ffffff;
    margin-right: 20px;
    border-radius: 2px;
    padding: 8px 25px;
    font-size: 1rem;
    background-color: rgba(51, 51, 51, 0.4);
}

.play_btn:hover{
    transition: -webkit-transform 600ms;
    transition: transform 600ms;
    transition: transform 600ms, -webkit-transform 600ms;
    background-color: rgb(202, 39, 39);
}

.list_btn:hover{
    transition: -webkit-transform 400ms;
    transition: transform 400ms;
    transition: transform 400ms, -webkit-transform 400ms;
    color: #000;
    background-color: rgb(206, 200, 200);
}

.fade_bottom{
    height: 11rem;
    background-image: linear-gradient(180deg, transparent, rgba(31, 29, 29, 0.61),#000);
}

@media only screen and (min-width: 768px){
    .contents{
        padding-top: 220px;
    }

    .fade_bottom{
        height: 9rem;
    }
}
.navbar{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;

    transition-timing-function: ease-in;
    transition: all 500ms;
}

.navbarblack{
    background-color: #111;
}

.navbar__logo{
    position: fixed;
    left: 20px;
    width: 100px;
    object-fit: contain;
}

.navbar__avatar{
    position: fixed;
    right: 30px;
    width: 30px;
    object-fit: contain;
}
